<template>
  <!--    action="http://10.176.36.38:8443/api/scenarios/upload"-->
<!--  ="http://localhost:8443/api/scenarios/upload"-->
<!--  :http-request="doUpload"-->
  <el-upload
    class="scenario-upload"
    ref="upload"
    action="http:///10.176.36.38:8443/api/scenarios/upload"
    accept="application/json"
    :before-remove="beforeRemove"
    :on-success="handleSuccess"
    multiple
    :limit="1"
    :on-exceed="handleExceed"
    :file-list="fileList"
    :before-upload="beforeUpload"
    :auto-upload="false">
    <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
    <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传</el-button>
    <div slot="tip" class="el-upload__tip">只能上传json文件</div>
  </el-upload>
</template>

<script>
export default {
  name: 'ScenarioUpload',
  data () {
    return {
      fileList: [],
      filePath: ''
    }
  },
  methods: {
    handleExceed (files, fileList) {
      let message = '当前限制选择1个文件，本次选择了' + files.length + '个文件'
      if (files.length + fileList.length >= 2) {
        message = message + '，共选择了' + (files.length + fileList.length) + '个文件'
      }
      this.$alert(message, '提示', {
        confirmButtonText: '确定'
      })
    },
    beforeRemove (file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`)
    },
    clear () {
      this.$refs.upload.clearFiles()
    },
    beforeUpload (file) {
      // 限制上传文件的类型和大小
      const isJson = (file.type === 'application/json')
      const isLimit2M = (file.size / 1024 / 1024 < 5)
      if (!isJson) {
        this.$alert('上传失败，仅支持上传json文件，请重新选择json格式的文件并上传！', '提示', {
          confirmButtonText: '确定'
        })
      }
      if (!isLimit2M) {
        this.$alert('上传失败，上传的文件大小不能超过5MB！', '提示', {
          confirmButtonText: '确定'
        })
      }
      return isJson && isLimit2M
    },
    submitUpload () {
      this.$refs.upload.submit()
    },
    handleSuccess (resp) {
      if (resp && resp.code === 200) {
        this.filePath = resp.result
        this.$alert('上传成功，请继续选择其他测试信息', '提示', {
          confirmButtonText: '确定'
        })
        this.$emit('onUpload')
      } else {
        this.$alert(resp.message, '提示', {
          confirmButtonText: '确定'
        })
        this.$refs.upload.uploadFiles.pop()
      }
    }
  }
}
</script>

<style scoped>

</style>



